.container {
  max-width: 100vw;
  background-color: #e5e5e3;
  display: flex;

  padding-top: 2rem;
  margin-bottom: 4rem;
  div {
    flex: 1;
  }
  img {
    max-width: 50vw;
    flex: 1;
  }
}

.content {
  margin: auto;
  width: fit-content;
  text-align: center;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
    padding: 5rem 0;
    gap: 5rem;
    div {
    }
    img {
      max-width: 100vw;
    }
  }
}
